import React, { useEffect, useContext, useState } from 'react';
import { PieChart, Pie, Tooltip, Cell } from 'recharts';
import AuthContext from '../context/AuthContext';


const data = [
  { name: 'Group A', value: 400 },
  { name: 'Group B', value: 300 },
  { name: 'Group C', value: 300 },
  { name: 'Group D', value: 200 },
];

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
      {`${(percent * 100).toFixed(0)}%`}
    </text>
  );
};

const DashboardGraph = () =>{
  let {authTokens, logoutUser} = useContext(AuthContext)
  let [projectcount, setProjectcount] = useState([]);
  useEffect(() => {
    getGraphdata();
  }, [])
  let getGraphdata = async () => {
    let response = await fetch('/api/counts/', {
      headers:{
        'Content-Type':'application/json',
        'Authorization':'Bearer '+ String(authTokens.access),
    },
    })
    let data = await response.json()
    if(response.status === 200){
      setProjectcount(
        [
          { name: 'Completed', value: data.completed_projects_count },
          { name: 'On Process', value: data.ongoing_projects_count },
          { name: 'Pending', value: data.pending_projects_count },
        ]
      )
      console.log(data)
    }else if(response.statusText === 'Unauthorized'){
      logoutUser()
  }
    else{
      alert('Something went wrong!');
    }
  }
  return (<div className='row'>
    <div className="col-4 m-3 p-2 bg-white rounded">
      <span>Projects</span>
      <PieChart width={200} height={200}>
        <Pie
          data={projectcount}
          cx="50%"
          cy="50%"
          labelLine={false}
          label={renderCustomizedLabel}
          outerRadius={80}
          fill="#8884d8"
          dataKey="value"
        >
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>
        <Tooltip />
    </PieChart>
  </div>
  </div>)
}
export default DashboardGraph